







































export default {
	data(){
		return{
			userPhone:'',//手机号
			userpwd:'',//密码
			usercode:'',//验证码
			show:true,
			timer:null,
			count:'',
			type:1,//手机号码验证是否成功
		}
	},
	methods:{
		delUserPhone(){
			this.userPhone=''
		},
		delUserpwd(){
			this.userpwd=''
		},
		// 获取验证码
		getCode(){
			this.show = false;
			const TIME_COUNT = 60;    
			if (!this.timer) {        
				this.count = TIME_COUNT;        
				this.show = false;        
				this.timer = setInterval(() => {        
				if (this.count > 0 && this.count <= TIME_COUNT) {          
					this.count--;         
				} else {          
					this.show = true;          
					clearInterval(this.timer);          
						this.timer = null;         
					}        
				}, 1000)       
			}    
		},
		
		// 直接注册
		loginCode(){
			var phoneReg=/^1[345789]\d{9}$/;
			if(this.userPhone.trim()==''&&this.usercode.trim()==''&&this.userpwd.trim()==''){
				uni.showToast({
					title: '请将内容填写完整!',
					duration:2000,
					icon:'none'
				});
				return;
			}else if(!(phoneReg).test(this.userPhone)){
				uni.showToast({
					title: '您输入的的手机号码有误!',
					duration:2000,
					icon:'none'
				});
				return;
			}else if(this.userPhone.trim()==''){
				uni.showToast({
					title: '手机号码不能为空!',
					duration:2000,
					icon:'none'
				});
				return;
			}else if(this.usercode.trim()==''){
				uni.showToast({
					title: '验证码不能为空!',
					duration:2000,
					icon:'none'
				});
				return;
			}else if(this.usercode.trim().length<6){
				uni.showToast({
					title: '您输入的验证码有误!',
					duration:2000,
					icon:'none'
				});
				return;
			}else if(this.userpwd==''){
				uni.showToast({
					title: '您输入的密码不能为空!',
					duration:2000,
					icon:'none'
				});
				return;
			}else{
				uni.redirectTo({
					url:'../../myCenter/myOrder'
				})
			}
		},
	}
}



